<template>
    <div id='box'>
      <div>
        <img src="@/assets/images/my/greenpower_bg@2x.png" alt="" class="img1">
        <img src="@/assets/images/my/greenpower_img_empty 2.png" class="img2" v-if="no==1">
        <img src="@/assets/images/my/greenpower_img_half@2x.png" class="img2" v-else-if="no==2">
        <img src="@/assets/images/my/greenpower_img_full@2x.png" class="img2" v-else>
        <span class="num" v-if="no!=1">{{green_power}}分钟</span>
        <p class="title1" v-if="no==1">还没有环保电量哦！</p>
        <p class="title2" v-if="no==1">每次充电或者商场购物可以累积环保电量，1元可累积10分钟，2元20分钟以此类推，最高可累积{{upper_limit}}分钟。累积的环保电量可用来免费充电。</p>
        <div class="btn"  v-if="no!=1" @click="$router.push({path:'/saoma'})">我要兑换</div>
        <p class="title3" v-if="no!=1">累积的环保电量达到60分钟就能兑换了相同分钟数的免费充电了哦！</p>
      </div>
    </div>
</template>

<script>
import { gethuanbao } from '@/axios/getData';
export default {
    props: {

    },
    data() {
        return {
          no:1, // 1 没有  2有   3满
          green_power:'',   //  个人环保电量
          upper_limit:'',   //  环保电量上限
        };
    },
    computed: {

    },
    created() {
      this.getdata()
    },
    mounted() {

    },
    methods: {
      async getdata(){
        var res = await gethuanbao();
        if( res.data.green_power == res.data.upper_limit){
          this.no = 3
        } else if (res.data.green_power>0){
          this.no = 2
        } else {
          this.no = 1
        }

        this.green_power = res.data.green_power
        this.upper_limit = res.data.upper_limit
        console.log(res);
      },
    },
    components: {

    },
};
</script>

<style scoped lang="scss">
  .img1{
    height:100vh;
    width:100%;
  }
  .img2{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate3d(-50%,-50%,0);
    width:70pt;
    height:150pt;
  }
  .num{
    position: absolute;
    display: inline-block;
    margin-top:-40pt;
    top:50%;
    left:50%;
    transform: translate3d(-50%,-50%,0);
    font-size: 13pt;
  }
  .title1{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate3d(-50%,-50%,0);
    margin-top:100pt;
    color:#fff;
    font-size: 13pt;
  }
  .title2{
    position: absolute;
    top:50%;
    left:50%;
    transform: translatex(-50%);
    margin-top:130pt;
    color:#fff;
    font-size: 13pt;
    width:80%;
  }
  .btn{
    position: absolute;
    top:50%;
    left:50%;
    transform: translatex(-50%);
    margin-top:100pt;
    line-height: 30pt;
    width:35%;
    background: #15BDDB;
    color:#fff;
    font-weight: 600;
    text-align: center;
    font-size: 13pt;
    border-radius: 15pt; 
  }
  .title3{
    position: absolute;
    top:50%;
    left:50%;
    transform: translatex(-50%);
    margin-top:150pt;
    width:80%;
    color:#fff;
    font-size: 13pt;
  }
</style>



